import React, {useState} from "react";
import {Button} from "../styled";
import Modal from "../modal";
import {AnimatePresence} from "framer-motion";
import Alert from "../alert";

const OrderLarge = ({data,completeOrder,setOrderDetail}) => {
    const [orderComplete,setOrderComplete] = useState(undefined);
    return(
        <div className="bg-white rounded-lg w-80 mx-auto my-6 p-4">
            {data.user &&
            <>
                <p className="text-center">Uivatel: {data.user.firstname} {data.user.lastname}</p>
                <p className="text-center">Email: {data.user.email}</p>
            </>
            }
            <p className="text-center">Stav: {data.order.completed ? <span style={{color:"green"}}>Vyzen</span> : <span style={{color:"red"}}>Probh</span>}</p>
            {data.order.items.map((item,index)=>(
                <div className="my-4 text-center item-bordered" key={index}>
                    <p>{item.data[0].name}</p>
                    <div className="w-30 m-auto">
                        <img src={item.data[0].image} alt={item.data[0].image}/>
                    </div>
                    <p>Poet kus v objednvce: {item.count}</p>
                    <p>Cena za kus: {item.data[0].price} K</p>
                    <p>Celkov cena za tento produkt: {item.data[0].price * item.count} K</p>
                </div>
            ))}
            <p className="text-center my-4 font-size-20">Celkov cena objednvky: <span className="text-underline">{data.order.price} K</span></p>
            {data.user && !data.order.completed && <Button className="position-relative mx-0 my-6" style={{left:"50%",transform:"translateX(-50%)"}} onClick={()=>{setOrderComplete({text:"Objednvka dokonena",error:false}); setTimeout(()=>{setOrderComplete(undefined);completeOrder(data.order._id,setOrderDetail);},2000)}}>Dokonit objednvku</Button>}
            <AnimatePresence>
                {orderComplete &&
                <Modal toggle={setOrderComplete}>
                    <Alert message={orderComplete} setMessage={setOrderComplete}/>
                </Modal>
                }
            </AnimatePresence>
        </div>

    )
}

export default OrderLarge;