import * as Yup from "yup";

export const ProductSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "Pl krtk!")
    .max(50, "Pli dlouh!")
    .required("Povinn"),
  description: Yup.string()
    .min(2, "Pl krtk!")
    .max(500, "Pli dlouh!")
    .required("Povinn"),
  price: Yup.number()
    .required("Povinn"),
  image: Yup.string()
    .required("Povinn"),
});

export const encode = (data) => {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
};
