import * as Yup from "yup";

export const UserSchema = Yup.object().shape({
  firstname: Yup.string()
    .min(2, "Pl krtk!")
    .max(50, "Pli dlouh!")
    .required("Povinn"),
  lastname: Yup.string()
      .min(2, "Pl krtk!")
      .max(50, "Pli dlouh!")
      .required("Povinn"),
  username: Yup.string()
      .min(2, "Pl krtk!")
      .max(50, "Pli dlouh!")
      .required("Povinn"),
  email: Yup.string()
      .email("Neplatn email")
      .max(50, "Pli dlouh!")
      .required("Povinn"),
  password: Yup.string()
      .min(8, "Pl krtk!")
      .max(50, "Pli dlouh!")
      .required("Povinn"),
});

export const encode = (data) => {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(data[key]))
    .join("&");
};
